.Header {
    text-align: center;
}

.HeaderAuthContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    margin: 0px 20px 20px;
    padding: 0 20px 0 20px;
}

.CoinContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
}

.DarkModeCheckbox {
    margin-left: 5px;
    width: 1em;
    height: 1em;
    border: 2px solid black;
    border-radius: 5px;
    background-color: bisque;
    color: white;
}

.DarkModeCheckbox:checked {
    background-color: grey;
}

.TitleContainer {
    display: flex;
    flex-direction: row wrap;
    justify-content: center;
    align-items: center;
    margin-right: 40px;
}

.AppTitle {
    /* border: 1px solid black; */
    font-family: 'Bungee Shade', cursive;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 15px;
}

.SignContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* border: 1px solid black; */
}

.UserSign {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}